}
.info__control-mark {
  position: absolute;
  top: -5px;
  left: 0;
  width: 30px;
  height: 30px;
  background: #ffffff;
  outline: 1px solid #cdcdcd;
}
.info__control-input[type="radio"] + .info__control-mark {
  border-radius: 50%;
}
.info__control-input[type="radio"]:checked + .info__control-mark::after {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 14px;
  height: 14px;
  background-color: #68b738;
  border-radius: 50%;
  content: "";
}
.info__control-input[type="checkbox"]:checked + .info__control-mark {
  background-image: url("../../img/form/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}
